.card {
    padding-bottom: 96px;
    &__img, &__info {
        width: 50%;
        margin-top: 30px;
    }
    &__inner {
        padding-bottom: 96px;
        .title-h3 {
            text-align: left;
        }
    }
    &__img {
        display: flex;
        padding: 0 160px;
        div {
            background-position: center;
            background-repeat: no-repeat;
            background-size: 60%;
            background-color: var(--itemBg);
            min-height: 662px;
            width: 100%;
        }
    }
    &__price {
        text-transform: uppercase;
        margin: 55px 0 50px;
        font-size: 24px;
    }
    &__description {
        font-size: 24px;
        line-height: 1.2;
        &-item {
            margin-top: 20px;
            span:first-child {
                font-weight: 500;
            }
        }
    }
    &__action {
        margin-top: 50px;
        .btn-bg {
            margin-right: 30px;
        }
        .item__action-btn input + label {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 80px;
            height: 80px;
            background-color: transparent;
            border: 1px solid #bebebe;
            border-radius: 0;
            box-sizing: border-box;
            color: #bebebe;
            &:hover {
                color: var(--Red);
            }
        }
    }
    &__rating {
        align-items: center;
        margin-top: 22px;
        .btn-review {
            line-height: 1;
        }
        &-review {
            margin: 0 0 0 25px;
        }
    }
    &__review {
        margin: 0;
        color: var(--reviewColor);
        line-height: 1;
        &::after {
            content: "|";
            margin: 0 15px;
            display: inline-block;
            color: var(--itemBg);
        }
    }
}
@media(max-width: 1600px) {
    .card {
        &__img {
            padding: 0 80px;
        }
    }
}
@media(max-width: 1200px) {
    .card {
        &__img {
            padding: 0 15px;
        }
    }
}
@media(max-width: 991px) {
    .card {
        &__inner {
            flex-wrap: wrap;
        }
        &__img, &__info {
            width: 100%;
        }
        &__img {
            div {
                background-size: 50%;
                min-height: 1px;
                height: 90vw;
            }
        }
        &__description, &__price {
            font-size: 18px;
        }
    }
}
@media(max-width: 900px) {
    .card, .card__inner {
        padding-bottom: 60px;
    }
}
@media(max-width: 500px) {
    .card {
        &__rating {
            flex-wrap: wrap;
            &-review {
                width: 100%;
                margin: 20px 0 0;
            }
        }
        &__action {
            .item__action-btn input + label {
                width: 60px;
                height: 60px;
            }
        }
    }
}